<template>
    <div>
        <input type="text" placeholder="First name" v-model="firstName">
        <input type="text" placeholder="Second name" v-model="secondName">
        <input type="text" placeholder="Middle name" v-model="middleName">
        <input type="text" placeholder="Course" v-model="course">
        <input type="text" placeholder="Faculty" v-model="faculty">
        <input type="text" placeholder="Team" v-model="team">
        <input type="button" value="Save" @click="save">
    </div>
</template>

<script>
export default {
  name: 'StudentForm',
  props: ['students'],
  data () {
    return {
      firstName: '',
      secondName: '',
      middleName: '',
      course: '',
      faculty: '',
      team: ''
    }
  },
  methods: {
    save () {
      let student =
        {
          firstName: this.firstName,
          secondName: this.secondName,
          middleName: this.middleName,
          course: this.course,
          faculty: this.faculty,
          team: this.team
        }
      this.$http.post('api/user/student', student)
        .then(response => response.json()
          .then(data => {
            this.students.push(data)
            this.firstName = ''
            this.secondName = ''
            this.middleName = ''
            this.course = ''
            this.faculty = ''
            this.team = ''
          }))
    }
  }
}
</script>

