<template>
    <div>
        <student-row v-for="student in students"
            v-bind:key="student.id"
            :student="student"
            @editStudent="editStudent"
            @deleteStudent="deleteStudent">
        </student-row>
    </div>
</template>

<script>
import StudentRow from './StudentRow'
export default {
  name: 'StudentList',
  components: { StudentRow },
  props: ['students'],
  methods: {
    editStudent (student) {
      this.$http.put('api/user/student', { params: { id: student.id } })
        .then(response => response.json()
          .then(data => {
            let index = this.students.indexOf(data.id)
            this.students.splice(index, 1, data)
          }))
    },
    deleteStudent () {}
  }
}
</script>

